import React, { Component } from 'react';
import scrollTo from '../scrollTo';

class Header extends Component {
  constructor(props) {
    super(props);

    this.state = {
      style: {
        height: '0px'
      }
    };
  }

  componentDidMount() {
    // [NOTE]: CSS100vhSafariJS100%
    this.setState({
      style: {
        ...this.state.style,
        height: `${window.document.documentElement.clientHeight}px`
      }
    });
    this.eventHandler = this.onResize.bind(this);
    window.addEventListener('resize', this.eventHandler);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.eventHandler);
  }

  onResize() {
    this.setState({
      style: {
        height: `${window.document.documentElement.clientHeight}px`
      }
    });
  }

  onClick(targetSelector) {
    const targetElement = document.querySelector(targetSelector);
    const targetTop = targetElement.getBoundingClientRect().top;
    scrollTo(window.scrollY + targetTop);
  }

  render() {
    return (
      <header id='header' className='masthead d-flex' style={this.state.style}>
        <div className='container text-center my-auto'>
          <div className='row justify-content-center align-items-end'>
            <a
              className='header-logo'
              onClick={this.onClick.bind(this, '#about')}
              to='#about'
            />
          </div>
        </div>
        <div className='overlay'></div>
      </header>
    );
  }
}

export default Header;
