import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';

interface LayoutProps extends React.HTMLProps<HTMLDivElement> {
  children: React.ReactNode;
}

const Layout: React.FC<LayoutProps> = props => {
  useEffect(() => {
    const scriptDatas: React.ScriptHTMLAttributes<string>[] = [
      {
        src: 'https://code.jquery.com/jquery-3.5.1.slim.min.js',
        integrity:
          'sha384-DfXdz2htPH0lsSSs5nCTpuj/zy4C+OGpamoFVy38MVBnE+IbbVYUew+OrCXaRkfj',
        crossOrigin: 'anonymous'
      }
    ];

    const deferLoad = () => {
      if (scriptDatas.length === 0) return;

      const scriptData: React.ScriptHTMLAttributes<string> = scriptDatas.shift();
      const scriptTag: HTMLScriptElement = document.createElement('script');

      scriptTag.addEventListener('load', deferLoad);

      document.body.appendChild(scriptTag);

      Object.keys(scriptData).forEach((attr: string): any => {
        const value: string = scriptData[attr];
        scriptTag[attr] = value;
      });
    };

    deferLoad();
  }, []);

  return (
    <>
      <Helmet>
        <link
          rel='stylesheet'
          href='https://stackpath.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css'
          integrity='sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk'
          crossOrigin='anonymous'
        />
        <link
          rel='stylesheet'
          href='https://use.fontawesome.com/releases/v5.7.2/css/all.css'
          integrity='sha384-fnmOCqbTlWIlj8LyTjo7mOUStjsKC4pOpQbqyi7RrhN7udi9RwhKkMHpvLbHG9Sr'
          crossOrigin='anonymous'
        />
        <link
          rel='stylesheet'
          href='https://cdnjs.cloudflare.com/ajax/libs/simple-line-icons/2.4.1/css/simple-line-icons.min.css'
        />
        <link rel='stylesheet' href='./assets/layout.css' />
      </Helmet>
      <div>{props.children}</div>
    </>
  );
};

export default Layout;
