import React, { Component } from 'react';
import scrollTo from '../scrollTo';

class Navigation extends Component {
  constructor(props) {
    super(props);

    this.state = {
      opened: false
    };
  }

  clickHandler() {
    this.setState({ opened: !this.state.opened });
  }

  clickNav(targetSelector) {
    scrollTo(targetSelector);
  }

  render() {
    const Lists = ({ data }) =>
      data.map((d, i) => (
        <li key={i} className='sidebar-nav-item'>
          <a
            onClick={this.clickNav.bind(this, d.to)}
            className='text-white font-weight-bold'
            to={d.to}
          >
            {d.title}
          </a>
        </li>
      ));

    return (
      <div>
        <a
          className={`menu-toggle rounded bg-dark text-white ${
            this.state.opened ? 'active' : ''
          }`}
          onClick={this.clickHandler.bind(this)}
          to='#'
        >
          <i
            className={'fas ' + (this.state.opened ? 'fa-times' : 'fa-bars')}
          ></i>
        </a>
        <nav
          id='sidebar-wrapper'
          className={`bg-dark ${this.state.opened ? 'active' : ''}`}
          onClick={this.clickHandler.bind(this)}
        >
          <ul className='sidebar-nav'>
            <Lists
              data={[
                {
                  to: '#about',
                  title: 'About'
                },
                {
                  to: '#services',
                  title: 'Services'
                },
                {
                  to: '#portfolio',
                  title: 'Shop'
                },
                {
                  to: '#pricing',
                  title: 'Pricing'
                },
                {
                  to: '#contact',
                  title: 'Contact'
                }
              ]}
            />
          </ul>
        </nav>
      </div>
    );
  }
}

export default Navigation;
